// components/Header.js
"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { motion } from "framer-motion";

export default function Header() {
  const [scrolling, setScrolling] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrolling(window.scrollY > 50);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <motion.header
      className={`fixed w-full top-0 z-50 transition-all duration-300 backdrop-blur-lg ${
        scrolling ? "bg-black bg-opacity-90 shadow-lg border-b border-cyan-500" : "bg-transparent"
      }`}
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
    >
      <nav className="container mx-auto flex justify-between items-center px-6 py-4">
        <h1 className="text-3xl font-extrabold text-cyan-400 tracking-wide font-mono">
          ⚡ Raghav's Hub
        </h1>

        {/* Desktop Menu */}
        <ul className="hidden md:flex space-x-8 text-lg font-semibold">
          {["About", "Projects", "Skills", "Experience", "Contact"].map((item) => (
            <li key={item}>
              <Link href={`#${item.toLowerCase()}`} className="text-white hover:text-cyan-300 transition-all duration-300">
                {item}
              </Link>
            </li>
          ))}
        </ul>

        {/* Mobile Menu */}
        <button className="md:hidden text-cyan-300 text-2xl">☰</button>
      </nav>
    </motion.header>
  );
}